@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&family=Ubuntu:wght@400;500;700&display=swap');
.heading-link {
  padding: 0.7rem ;
  text-align: center;
  background: #FFC628 no-repeat;
  background-size: cover;
  background-position: center;
}
.heading-link h3 {
	font-family: 'Ubuntu', sans-serif;
  font-size: 2rem;
  text-transform: capitalize;
  color: black;
}
.heading-link p {
	font-family: 'Ubuntu', sans-serif;
  font-size: 1.1rem;
  line-height: 2;
  color: black;
}

.heading-link p a {
  color: #444;
}

.heading-link p a:hover {
	color: crimson;
	transition: all 0.6s ease;
  
}
.box-container{
    padding: 12rem 9em 4rem ;
    display: flex;
    flex-wrap: wrap;
    gap: 3rem;
}
.box-container1{
  padding: 1rem 9em 10rem ;
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
  
}

.box{
  cursor: pointer;
  height: 16rem;
  flex:1 1 1rem;
  text-align: center;
  padding: 2.5rem;
  background: #254b57;
  border-radius: 2rem;
  box-shadow:1rem 2rem 1rem rgba(0, 0, 0, 0.1);
  transition: all 0.2s ease;
}
.box:hover{
  scale: 1.05;
  transition: all 0.2s ease;
}

.box div{
  transition: all 0.6s ease;
}

.box h2{
  text-align: center;
  color: white;
  font-family: 'Ubuntu', sans-serif;
  font-size: 2rem;
  opacity: 0;
  transition: all 1s ease;
  transform: translateY(135px);
  
}
.box:hover h2{
  transform: translateY(21px);
  
  opacity: 1;
}
.box:hover div{
  transform: translateY(29px);
}

