@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&family=Ubuntu:wght@400;500;700&display=swap');
.heading-link {
    padding: 0.7rem ;
    text-align: center;
    background: #FFC628 no-repeat;
    background-size: cover;
    background-position: center;
}
.heading-link h3 {
	font-family: 'Ubuntu', sans-serif;
  font-size: 2rem;
  text-transform: capitalize;
  color: black;
}
.heading-link p {
	font-family: 'Ubuntu', sans-serif;
  font-size: 1.1rem;
  line-height: 2;
  color: black;
}

.heading-link p a {
  color: #444;
}

.heading-link p a:hover {
	color: crimson;
	transition: all 0.6s ease;
  
}

.portfolio-container{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, auto));
    grid-gap: 3rem;
    align-items: center;
    margin: 12rem 6rem;
    gap: 2rem;
}
.boxx{
    height: 370px;
    border-radius: 10px;
    transition: all 0.4s;
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 10px;
    position: relative;
}

.inner{
    position: absolute;
    bottom: 33px;
    left: 35px;
}

.inner h3{
    font-size: 1.3rem;
    color: #424242;
    font-weight: 600;
    margin-bottom: 10px;
}
.inner button{
    background: crimson ;
    font-size: 1rem;
    border: 2px solid crimson;
    color: white;
    padding: 7px 22px;
    border-radius: 100px;
    cursor: pointer;
}
.inner button:hover{
    background: transparent;
    border: 2px solid #c6c9d8;
    transition: 0.5s;
    color: crimson;
}
.restaurant{
    width: 80%;
     position: absolute;
    margin-top: -1.5rem;
}
.bounce{
    position: absolute;
    margin-top: -5.5rem;
}
.tower{
    position: absolute;
    margin-top: -5.5rem;
}