@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&family=Ubuntu:wght@400;500;700&display=swap');

*{
	margin:0;
	padding: 0;
	box-sizing: border-box;
	text-decoration: none;
}

html{
	scroll-behavior: smooth;
}
.max-width{
	max-width: 2000px;
	padding: 0 128px;
	margin: auto;
}



:root{
    --blue: #287bff;
    --white: #fff;
    --grey: #f5f5f5;
    --black1: #222;
    --black2: #999;
}
.navigation{
    position: fixed;
	z-index: 1000;
    width: 80px;
    height: 100%;
    background: var(--blue);
    border-left: 10px solid var(--blue);
    transition: 0.6s;
    overflow: hidden;
}
.navigation:hover{
	width: 280px;
}
.navigation ul{
    position: absolute;
    top: 1rem;
    left: 0;
    width: 100%;

}
.navigation ul li{
    position: relative;
    width: 100%;
    list-style: none;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
}
.navigation ul li:hover{
    background: var(--white);
}
.navigation ul li:nth-child(1){
    margin-bottom: 40px;
    pointer-events: none;
}
.navigation ul li a{
    position: relative;
    display: block;
    width: 100%;
    display: flex;
    text-decoration: none;
    color: var(--white);
}
.navigation ul li:hover a,
.navigation ul li.hovered a{
    color: var(--blue);
}
.navigation ul li a .icon{
    position: relative;
    display: block;
    min-width: 60px;
    height: 60px;
    line-height: 70px;
    text-align: center;
}
.navigation ul li a .title{
	font-size: 1.2rem;
	font-family: 'Ubuntu', sans-serif;
    position: relative;
    display: block;
    padding: 0 10px;
    height: 60px;
    line-height: 60px;
    text-align: start;
    white-space: nowrap; 
}

/* outside */
.navigation ul li:hover a::before,
.navigation ul li.hovered a::before{
    content: '';
    position: absolute;
    right: 0;
    width: 50px;
    height: 50px;
    background: transparent;
    top: -50px;
    border-radius: 50%;
    box-shadow: 35px 35px 0 10px var(--white);
    pointer-events: none;

}
.navigation ul li:hover a::after,
.navigation ul li.hovered a::after{
    content: '';
    position: absolute;
    right: 0;
    width: 50px;
    height: 50px;
    background: transparent;
    bottom: -50px;
    border-radius: 50%;
    box-shadow: 35px -35px 0 10px var(--white);
    pointer-events: none;

}

.titlee{
	position: relative;
	font-family: 'Ubuntu', sans-serif;
    display: block;
    padding: 0 10px;
    height: 60px;
    line-height: 60px;
    text-align: start;
    white-space: nowrap; 
	font-size: 1.7rem;
	cursor: pointer;
}
.last{
	color: crimson;
	font-size: 1.9rem;
	cursor: pointer;
}


/* main */

.mn{
    position: absolute;
    width: calc(100% - 80px);
    left: 80px;
    min-height: 100vh;
    background: rgb(241, 237, 237);
    transition: 0.5s;
}




/*menu button styling */

.icon-position{
  position: fixed;
  bottom: 20px;
  right: 25px;
  z-index: 1;
}


@keyframes movebtn {
  0%{
    transform: translateY(0px);
  }
  25%{
    transform: translateY(30px);
  }
  50%{
    transform: translateY(0px);
  }
  75%{
    transform: translateY(-30px) ;
  }
  100%{
    transform: translateY(0px);
  }
}


/*--Basliq---*/
.heading-link {
	
	padding: 0.7rem ;
	text-align: center;
	background: #FFC628 no-repeat;
	background-size: cover;
	background-position: center;
}
.heading-link h3 {
	font-family: 'Ubuntu', sans-serif;
  font-size: 2rem;
  text-transform: capitalize;
  color: black;
}
.heading-link p {
	font-family: 'Ubuntu', sans-serif;
  font-size: 1.1rem;
  line-height: 2;
  color: black;
}

.heading-link p a {
  color: #444;
}

.heading-link p a:hover {
	color: crimson;
	transition: all 0.6s ease;
  
}

/*home section*/
.home{
	display: flex;
	height: 100vh;
	color: #fff;
	min-height: 500px;
	font-family: 'Ubuntu', sans-serif;
	overflow: hidden;
}
.home .max-width{
	margin-top: 12rem;
}
.home .home-content .text-1, .text-2, .text-3, .text-3 span{
	
	animation: animaHome 3s ease forwards;
}
@keyframes animaHome{
	to{
		opacity: 1;
	}
}
.home .home-content .text-1{
	color: black;
	font-size: 22px;
	opacity: 0;
}
.home .home-content .text-2{
	color: black;
	font-size: 60px;
	font-weight: 600;
	margin-left: -3px;
	opacity: 0;
}
.home .home-content .text-3{
	color: black;
	font-size: 33px;
	margin:5px 0;
	opacity: 0;
}
.home .home-content .text-3 span{
	color: crimson;
	font-weight: 450;
	opacity: 0;
}

.home .home-content .CV{
	width: 70%;
	display: grid;
	margin-top: 40px;
	cursor: pointer;
}
.rightside{
	position: relative;
	width: 110%;
	height: 100%;
	background: white;
	margin-left: 10rem;
	align-items: center;
	justify-content: center;
	display: flex;
	border-top-left-radius: 50%;
	border-bottom-left-radius: 58%;
	
	
}

.experience{
	margin-left: 60px;
}



/* ----grator----*/
.btn{
	display: inline-block;
	background: #fff;
	color: #000;
	padding: 1rem 2rem;
	border:1 solid transparent;
	font-weight: 500;
	transition: all 0.5s ease;
	border-radius: 7px;
}
.btn:hover{
	background: transparent;
	border-color: #FFC628;
	color: #FFC628;
}
.btn-primary{
	background: #FFC628;
	color: #000;
}

.contact_container{
	background: #2e3267;
	padding: 4rem;
	display: grid;
	grid-template-columns: 40% 60%;
	gap: 4rem;
	height: 30rem;
	margin: 7rem auto;
}
.contact_aside{
	width: 25rem;
	background: #6c63ff;
	padding: 3rem;
	border-radius: 1rem;
	position: relative;
	bottom: 9.5rem;
	left: 3.5rem;
}
.image_aside{
	width: 12rem;
	margin-bottom: 2rem;
}
.image_aside img{
	width: 12rem;
	height: 12rem;
}
.contact_aside h2{
	text-align: left;
	margin-bottom: 1rem;
	font-family: 'Ubuntu', sans-serif;
	color: #fff;
	font-size: 2rem;
}
.contact_aside p{
	font-size: 1.2rem;
	margin-bottom: 2rem;
	font-family: 'Ubuntu', sans-serif;
	color: #fff;
}
.contact_details li {
	display: flex;
	gap: 1rem;
	align-items: center;
	margin-bottom: 1rem;
	font-family: 'Ubuntu', sans-serif;
	color: #fff;
}
.contact_socials{
	transition: transform 250ms;
  	display: flex;
	gap: 0.7rem;
	margin-top: 1.4rem;
	
}
.contact_socials a { 
	
	padding: 0.9rem;
	border-radius: 50%;
	font-size: 2rem;
	transition: all 0.5s ease;
	color: #fff;
}
.contact_socials a:hover {
	background: transparent;
	transform: translateY(-15px);
}

.contact_form{
	display: flex;
	flex-direction: column;
	gap:1.2rem;
	margin-right: 4rem;
	font-family: 'Ubuntu', sans-serif;
}
.form_name{
	display: flex;
	gap:1.2rem;
	font-family: 'Ubuntu', sans-serif;
}
.contact_form input[type="text"]{
	width: 50%;
	font-family: 'Ubuntu', sans-serif;
}
input, textarea{
	width: 100%;
	padding: 1rem;
	background: #1f2641;
	color: #fff;
	font-family: 'Ubuntu', sans-serif;
}
.contact_form .btn{
	width: max-content;
	margin-top: 1rem;
	cursor: pointer;
}


/*---Footer---*/
.footer {
	display: flex;
	align-items: center;
	position: relative;
	margin-top: 10rem;
  }
  .footer img{
	  height: 30rem;
  }
  .f-content {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	width: 100%;
	margin-top: 10rem;
	color: white;
	font-size: 1.3rem;
	cursor: pointer;
  }
  .f-icons {
	  width: 27%;
	display: flex;
  }
  
  @media screen and (max-width: 480px){
	.f-content{
	  transform: scale(.5);
	}
  }
  
